@import "./utility.scss";

/* Table */

.table-wrapper {
    margin: 0 auto;
    max-width: 100rem;
    min-width: 80%;
    overflow-x: scroll;
    scrollbar-width: thin;
    scrollbar-color: $primary-clr $light-clr;
    &::-webkit-scrollbar {
        width: 1.5rem;
    }
    &::-webkit-scrollbar-track {
        border-radius: 1rem;
        box-shadow: inset 0 0 0.5rem $primary-clr-500;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 1rem;
        background-color: $primary-clr;
    }
}

table {
    min-width: 80rem;
    table-layout: fixed;
    margin: 4rem auto 0rem auto;
    border-collapse: collapse;
    border: 3px solid $primary-clr-500;
    font-size: 1.5rem;
    background-color: $primary-clr-500;
    thead {
        color: $white-clr;
        th {
            letter-spacing: 0.2rem;
            font-weight: bold;
            font-size: 1.8rem;
            &:nth-child(3) {
                width: 20%;
            }
            &:nth-child(5),
             :nth-child(6),
             :nth-child(7),
             :nth-child(8),
             :nth-child(9) {
                width: 10%;
            }
        }
    }
    th,
    td {
        padding: 2rem;
        text-align: center;
    }
    tbody {
        tr:nth-child(odd) {
            background: linear-gradient(250deg, $primary-clr, $primary-clr-500);
            color: $white-clr;
        }
        tr:nth-child(even) {
            background: linear-gradient(250deg, $white-clr, $light-clr);
            color: $black-clr;
        }
        td {
            letter-spacing: 1px;
        }
    }
    caption {
        padding: 20px;
        font-style: italic;
        caption-side: bottom;
        color: $black-clr-500;
        text-align: right;
        font-size: 2rem;
        letter-spacing: 1px;
    }
}


/* Form */

.container {
    display: grid;
    place-items: center;
    .form {
        border-radius: 2rem;
        box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
        padding: 4rem;
        margin: 4rem;
        background-color: $light-clr;
        font-size: 1.8rem;
        width: 44rem;
        &-error {
            padding: 1.5rem 2rem;
            margin: 1.5rem;
            line-height: 3rem;
            letter-spacing: 0.08rem;
            text-align: center;
            background-color: $danger-clr;
            color: $white-clr;
            font-weight: bold;
            font-size: 2rem;
            border-radius: 2rem;
        }
        input {
            padding: 1rem 2rem;
            margin: 1.2rem;
            border-radius: 2rem;
            text-align: center;
            outline: none;
            font-size: 1.8rem;
            border: 3px solid $primary-clr-500;
            transition: $smooth;
            &:hover,
            &:focus {
                border-color: $primary-clr;
            }
        }
        .radio {
            margin: 1rem;
            div {
                margin: 0 1rem;
            }
        }
        .confirmation {
            margin: 3rem 0;
            .btn {
                margin: 0 1rem;
                padding: 0.6rem 2.5rem;
                font-size: 2.5rem;
            }
        }
    }
}


/* Modal */

.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 50rem;
    transform: translate(0, 100%) rotateY(60deg) scale(0);
    transform-origin: center;
    transition: $smooth;
    background-color: $white-clr;
    z-index: 1;
    .modal-header {
        border-bottom: 4px solid $black-clr;
        .heading {
            background: linear-gradient(60deg, $primary-clr, $primary-clr-500);
            text-align: center;
            font-size: 3rem;
            font-weight: 700;
            padding: 1rem;
            color: $white-clr;
        }
    }
    .modal-body {
        padding: 1rem;
        form {
            z-index: 3;
            margin: 0;
            padding: 0;
        }
    }
    &.modal-active {
        transform: translate(-50%, -50%) rotate(0deg) scale(1);
    }
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    transition: $smooth;
    opacity: 0;
    z-index: 1;
    pointer-events: none;
    &.overlay-active {
        pointer-events: all;
        cursor: pointer;
        opacity: 1;
    }
}